import { LitElement, html } from 'lit-element';
import { dispatchCustomEvent } from '../lib/events';
import { repeat } from 'lit-html/directives/repeat';

/**
 * A main nav
 *
 * @fires gv-main-nav:change - Custom event when nav link change
 *
 * @attr {Array} links - definition of links [{href: String, selected: Boolean, title: String}]
 *
 */
export class GvMainNav extends LitElement {

  static get properties () {
    return {
      links: { type: Array, attribute: false },
    };
  }

  _onClick ({ target: { title } }) {
    this.links = this.links.map((link) => {
      link.selected = link.title === title;
      return link;
    });
    dispatchCustomEvent(this, 'change', this.links);
  }

  render () {
    return html`
        <nav>
          ${repeat(this.links, ({ title }) => title, ({ href, title, selected }) => html`
            <gv-nav-link 
            @click=${this._onClick} 
            href="${href}" 
            title="${title}" 
            ?selected="${selected}">${title}</gv-nav-link>
      `)}
        </nav>`;
  }

}

window.customElements.define('gv-main-nav', GvMainNav);
